@import '../App.scss';


.header {
    height: 100vh;
    width: 4%;
    color: $color-icons;
    background-color: $bg;
    font-size: 18px;
    font-weight: lighter;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 70px 0 50px;
    transition: background 1s linear 0s;

    ._icon-moon, ._icon-sun, ._icon-palette {
        margin: 5px;
    }

    .nav {
        .ul {
            padding: 0;
            text-align: center;
            
            .li {
                list-style-type: none;
                margin-bottom: 30px;
                z-index: 2;
                // position: relative;
                

                .navLink {
                    display: block;
                    color: $color-icons;
                    width: inherit;
                    text-decoration: none;
                }

                .active {
                    color: $color-icons-active;
                    background-image: $bg-image;
                    -webkit-background-clip: text;
                    -webkit-text-fill-color: transparent;
                }
            }

            /*для всалывающей подсказки*/
            [dataTooltip] {
                position: relative; /* Относительное позиционирование */ 
            }


            [dataTooltip]::after {
                content: attr(dataTooltip); /* Выводим текст */
                position: absolute; /* Абсолютное позиционирование */
                left: 0; top: 0; /* Положение подсказки */
                width: max-content;
                color: $color-text; /* Цвет текста */
                background: $bg; /*цвет фона */
                padding: 0.5em; /* Поля вокруг текста */
                box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3); /* Параметры тени */
                border-radius: 5px;
                pointer-events: none; /* Предотвратить взаимодействие курсора мыши с подсказкой */
                opacity: 0; /*Подсказка невидима */
                transition: 1s; /* Время появления подсказки */
                z-index: 1;
            } 

            [dataTooltip]::after::before {
                // content: attr(dataTooltip);
                // width: 0;
                // height: 0;
                // border-top: 10px solid transparent;
                // border-bottom: 10px solid transparent;
                // border-right: 10px solid #0067A3;
                // position: absolute;
                // left: -5px;
                // top: 0;
                // transform: translateY(-50%);
            }

            [dataTooltip]:hover::after {
                opacity: 1; /* Показываем подсказку */
                top: -0.5em; /*Положение подсказки */
                left: 2.2em;
            }




        }
    }

    .settings {
        position: relative;

        .theme {
            text-align: center;
            margin-bottom: 20px;
            cursor: pointer;
            display: flex;
            flex-direction: column-reverse;
            color: $color-text;
            // background-image: $bg-image;
            // -webkit-background-clip: text;
            // -webkit-text-fill-color: transparent;

            .dropdownTheme {
                display: none;
                transition: display 1s linear 0s;
                // transition-duration: 1s;
                // transition-property: display;
                // transition-delay: 3s;

                .dDcontent:hover {
                    color: $color-icons-active;
                    background-image: $bg-image;
                    -webkit-background-clip: text;
                    -webkit-text-fill-color: transparent;
                }

                .pink {
                    color: #be9ca1;
                }

                .green {
                    color: #7b8274;
                }
            }
        }

        .theme:hover .dropdownTheme {
            display: block;
            display: flex;
            flex-direction: column-reverse;
        }

        .language {
            text-align: center;
            font-weight: bolder;
            cursor: pointer;
            // font-weight: bold;
            color: $color-title;
            background-image: $bg-image;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
        }
        
    }

}



@media screen and (max-device-aspect-ratio: 8/5) { 
    .header {
        height: 60px;
        width: 100%;
        flex-direction: row;
        padding: 0 50px 0 70px;
        position: sticky;
        top: 0;
        z-index: 2;
    
        .nav {
            display: flex;
            align-items: center;
            
            .ul {
                display: flex;

                .li {
                    margin-bottom: 0;
                    margin-right: 30px;
                }
            }
        }
    
        .settings {
            display: flex;
            align-items: center;
    
            .theme {
                margin-bottom: 0px;
                margin-right: 20px;
                flex-direction: row-reverse;
            }

            .theme:hover .dropdownTheme {
                flex-direction: row-reverse;
            }
        }
    }
}




@media screen and (max-width: 950px) { 
    .header {
        height: 60px;
        width: 100%;
        flex-direction: row;
        padding: 0 50px 0 50px;
        position: sticky;
        top: 0;
        z-index: 2;
    
        .nav {
            display: flex;
            align-items: center;
            
            .ul {
                display: flex;

                .li {
                    margin-bottom: 0;
                    margin-right: 30px;
                }
            }
        }
    
        .settings {
            display: flex;
            align-items: center;
    
            .theme {
                margin-bottom: 0px;
                margin-right: 20px;
                flex-direction: row-reverse;
            }

            .theme:hover .dropdownTheme {
                flex-direction: row-reverse;
            }
        }
    }
}


@media screen and (max-width: 700px) {
    .header {
        height: 45px;
        font-size: 14px;
        padding: 0 20px;
    }
}